@font-face {
    font-family: 'Graphik Regular';
    src: url('../fonts/graphik-regular-web/Graphik-Regular-Web.eot');
    src: url('../fonts/graphik-regular-web/Graphik-Regular-Web.eot#iefix') format('embedded-opentype'), url('../fonts/graphik-regular-web/Graphik-Regular-Web.woff2') format('woff2'), url('../fonts/graphik-regular-web/Graphik-Regular-Web.woff') format('woff'), url('../fonts/graphik-regular-web/Graphik-Regular-Web.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Graphik Bold';
    src: url('../fonts/graphik-bold-web/Graphik-Bold-Web.eot');
    src: url('../fonts/graphik-bold-web/Graphik-Bold-Web.eot#iefix') format('embedded-opentype'), url('../fonts/graphik-bold-web/Graphik-Bold-Web.woff2') format('woff2'), url('../fonts/graphik-bold-web/Graphik-Bold-Web.woff') format('woff'), url('../fonts/graphik-bold-web/Graphik-Bold-Web.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Cervo-Regular';
    src: url('../fonts/cervo-regular/322B3C_0_0.eot');
    src: url('../fonts/cervo-regular/322B3C_0_0.eot?#iefix') format('embedded-opentype'), url('../fonts/cervo-regular/322B3C_0_0.woff2') format('woff2'), url('../fonts/cervo-regular/322B3C_0_0.woff') format('woff'), url('../fonts/cervo-regular/322B3C_0_0.ttf') format('truetype');
}

//
.regular {
    font-family: $font-regular;
}

.bold {
    font-family: $font-bold;
}

.cervo {
    font-family: $font-cervo;
    text-transform: uppercase;
    letter-spacing: .35rem;
}

.dark-blue {
    color: $dark-blue;
}

.brown {
    color: $brown;
}

.light {
    color: white;
}

//
p {
    font-size: 1.5rem;
    line-height: 1.5;
}

a {
    text-decoration: none;
    border-bottom: 1px solid;
    color: $black;
    &:hover {
        border-bottom: none;
    }
    img {
        vertical-align: middle;
        &:hover {
            //opacity: 0.85;
        }
    }
}

h1 {
    @extend .cervo;
    @extend .light;
    font-size: 3rem;
    font-weight: normal;
    padding: 0;
}

h2 {
    @extend .cervo;
    @extend .brown;
    font-size: 2.5rem;
    font-weight: normal;
    padding: 0;
}

h3 {
    font-size: 2rem;
    line-height: 1.5;
    font-weight: 400;
}

ul {
    text-align: left;
    li {
        font-size: 2rem;
        list-style-type: none;
        padding-bottom: 1.5rem;
        &::before {
            content: '— ';
            margin-left: -1em;
        }
    }
}

strong {
    @extend .bold;
}

.quotes {
    &:lang(ru) {
        &::before {
            content: '«';
            margin-left: -.5em;
        }
        &::after {
            content: '»';
        }
    }
    &:lang(en) {
        &::before {
            content: '“';
            margin-left: -.5em;
        }
        &::after {
            content: '”';
        }
    }
}

.emdash {
    display: block;
    &::before {
        content: '— ';
        margin-left: -1.3em;
    }
}