///

.fullwidth {
    width: 100vw;
}

.width1000 {
    width: 1000px;
    position: relative;
    margin: 0 auto;
    text-align: center;
}

.mir {
    transform: scaleX(-1);
    filter: FlipH;
}

.trans {
    transition: all .4s;
}

.transparent {
    pointer-events: none;
}

.no-mobile {
    @media screen and (max-width: 1000px) {
        opacity: 0;
        display: none;
        visibility: hidden;
    }
}

.margin-auto {
    margin: 0 auto;
}

.will-change {
    will-change: transform;
    transform: translateZ(0);
}

.clearfix {
    display: block;
    zoom: 1;
    &:after {
        content: " ";
        display: block;
        font-size: 0;
        height: 0;
        clear: both;
        visibility: hidden;
    }
}

///

.absolute {
    position: absolute;
}

.relate {
    position: relative;
}

.center {
    text-align: center;
}